.container{display:flex;flex-direction:column;flex:1}.titleWrapper{gap:8px;display:flex;flex-direction:column;color:#fffce3;margin-block-end:60px}@media(min-width: 1024px){.titleWrapper{flex-direction:row;gap:12px;align-items:flex-end}}.progressWrapper{width:100%;height:10px;background-color:rgba(255,252,227,.4);border-radius:12px;margin-bottom:12px;position:relative}@media(max-width: 749px){.progressWrapper{height:12px;margin-bottom:4px}}.progressBar{height:100%;background-color:#fffce3;border-radius:12px;transition:width .3s ease-in-out}.valueLabels{display:flex;justify-content:space-between;align-items:center;font-size:20px;font-weight:500;color:#dcdcdc}.subtitle{font-weight:500}.anchorContainer{position:absolute;display:flex;flex-direction:column;align-items:center;z-index:10;gap:8px;transform:translate(-50%, -30%)}@media(max-width: 749px){.anchorContainer{gap:4px;transform:translate(-50%, -40%)}}.anchorContainer p{font-size:28px;font-weight:600}@media(max-width: 749px){.anchorContainer p{font-size:20px;font-weight:500}}.anchorContainer .anchor{border-radius:50%;background-color:#fffce3;display:flex;justify-content:center;align-items:center}.anchorContainer .anchor .iconContainer{position:relative}.anchorContainer .default{width:40px;height:40px}.anchorContainer .default .iconContainer{width:19px;height:30px}.anchorContainer .large{width:60px;height:60px}.anchorContainer .large .iconContainer{width:28px;height:44px}