.container{z-index:1;margin-block-start:60px;margin-block-end:60px}.container .roadmapContainer{position:relative}.container .roadmapTile{margin-block-end:-30px}.container .roadmapTile:nth-child(odd){width:65%}.container .roadmapTile:nth-child(even){padding-inline-start:55px}@media(max-width: 749px){.container .roadmapTile{width:80%}.container .roadmapTile:nth-child(odd){width:75%}.container .roadmapTile:nth-child(even){padding-inline-start:15px}}.container .buttonControls{margin-block-start:60px}@media(min-width: 1024px){.container .buttonControls{display:none}}