.teamMembersContainer{display:block;padding-block-start:40px;padding-block-end:40px;position:relative}.teamMembersContainer .teamMembersList{display:none}@media(min-width: 1024px){.teamMembersContainer .teamMembersList{display:grid;gap:40px;grid-template-columns:repeat(4, minmax(218px, 1fr));grid-template-rows:repeat(3, minmax(243px, 1fr))}}@media(min-width: 1800px){.teamMembersContainer .teamMembersList{grid-template-columns:repeat(4, minmax(248px, 1fr));grid-template-rows:repeat(3, minmax(283px, 1fr))}}.teamMembersContainer .teamMembersSlider{display:block;height:140px;width:100%;position:relative;margin:0 auto}@media(min-width: 1024px){.teamMembersContainer .teamMembersSlider{display:none}}.teamMembersContainer .teamMembersSlider :global(.slick-slide){height:140px;display:flex;justify-content:center;padding:0 10px}@media(min-width: 1024px){.teamMembersContainer{display:flex;flex-direction:row;gap:40px;padding-block-start:60px;padding-block-start:60px}}.sliderItem{width:100%;height:100%;display:block;margin:0 auto}