.gridContainer{padding-block-start:60px;padding-block-end:60px;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);gap:20px}.tileMain,.tileSecondary,.tileTertiary{box-shadow:inset 0px 6px 12px rgba(0,0,0,.45);border-radius:60px}.tileMainWrapper{position:relative;height:884px;grid-column:span 2/span 2;grid-row:span 2/span 2;display:flex}.tileMainWrapper .starsDecoration{left:70%}.tileMainWrapper .starsDecorationXAxis{margin-left:70%;left:-15px}.tileMainWrapper .starsDecorationYAxis{margin-left:70%;left:-50px;top:-56px;transform:rotate(90deg)}.tileMainWrapper .wigwamDecoration{left:-20px;top:30%;transform:translateY(-30%);border-top-right-radius:60px}.tileMainWrapper .wigwamDecorationXAxis{margin-left:-15px;left:-26px;transform:rotate(270deg);top:229px}.tileMainWrapper .wigwamDecorationYAxis{transform:rotate(3600deg);margin-top:-15px;top:254px}.tileMainWrapper .tileMain{padding:40px;display:flex;background-image:url("/assets/aboutImage.png");background-repeat:no-repeat;background-size:cover;background-position:top center}.tileMainWrapper .tileMain .textContainer{margin-block-start:auto;max-width:70%;color:#fffce3;padding:32px;background:rgba(255,252,227,.15);border:3px solid rgba(255,252,227,.2);box-shadow:4px -1px 8px rgba(61,62,61,.15);backdrop-filter:blur(17.5px);border-radius:40px}.tileMainWrapper .tileMain .textContainer h4{margin-block-end:28px}.tileSecondaryWrapper{position:relative;grid-column-start:3}.tileSecondaryWrapper .tileSecondary{height:100%;width:100%;background-image:url("/assets/aboutImage2.png");background-repeat:no-repeat;background-size:cover;background-position:top center;border-top-left-radius:40px}.tileSecondaryWrapper .moonDecoration{left:-20px;top:25%;transform:translateY(-25%)}.tileSecondaryWrapper .moonDecorationXAxis{margin-left:-15px;left:-26px;transform:rotate(270deg);top:78px}.tileSecondaryWrapper .moonDecorationYAxis{transform:rotate(3600deg);margin-top:-15px;top:103px}.tileSecondaryWrapper .logoDecoration .logoDecorationWrapper{padding:13px 21px}.tileSecondaryWrapper .logoDecoration .logoDecorationWrapper .imageContainer{width:234px;height:55px;position:relative}.tileSecondaryWrapper .logoDecorationXAxis{right:-40px;margin-right:0;transform:rotate(90deg);bottom:67px}.tileSecondaryWrapper .logoDecorationYAxis{transform:rotate(90deg);bottom:-55px;left:47%}.tileTertiaryWrapper{position:relative;grid-column-start:3;grid-row-start:2}.tileTertiaryWrapper .tileTertiary{height:100%;width:100%;background-image:url("/assets/aboutImage1.png");background-repeat:no-repeat;background-size:cover;background-position:top center;border-bottom-left-radius:40px}.tileTertiaryWrapper .featherDecoration{left:-20px;bottom:15%;transform:translateY(-15%)}.tileTertiaryWrapper .featherDecorationXAxis{margin-left:-15px;left:-26px;transform:rotate(270deg);top:229px}.tileTertiaryWrapper .featherDecorationYAxis{transform:rotate(3600deg);margin-top:-15px;top:254px}@media(min-width: 1024px){.mobileMainTile{display:none}}